import React, { Component } from 'react';
import styled from 'styled-components';

const TimebarWrapper = styled.div`
	font-size: 1.6rem;
	text-transform: uppercase;
	letter-spacing: 1px;
	.seconds_s {
		text-transform: lowercase;
	}
	.toggle_time {
		margin-left: 4rem;
		border: none;
		color: #fff;
		text-transform: uppercase;
		font-size: 1.6rem;
		background: #e82e3b;
		border-radius: 4px;
		padding: 3px 15px;
		font-weight: 600;
	}
`;

class timebar extends Component {
	handleTimerToggle = () => {
		if (this.props.timerIsPaused) {
			this.props.startTimer();
		} else {
			this.props.stopTimer();
		}
	};

	render() {
		return (
			<TimebarWrapper>
				<span>
					Time Remaining: {this.props.timer}
					<span className="seconds_s">s</span>
				</span>
				{/* inline if statement needed to determine what icon to show */}
				<button onClick={this.handleTimerToggle} className="toggle_time">
					{this.props.timerIsPaused ? 'Start' : 'Stop'} Timer
				</button>
			</TimebarWrapper>
		);
	}
}

export default timebar;
